body{
    font-size: 16px;
    font-family: Noto Sans JP, "Hiragino Sans";
}

.header{
    height: 100px;
    display: flex;
    z-index: 10000;
    padding: 0px 50px 0px 100px;
    vertical-align: middle;
    box-shadow: 0px 3px 6px rgba(0,0,0, .1)
}

.logo-wrapper{
    height:75px;
    margin: auto 0;
    img{
        width: 250px;
        height: 100%;
    }
}

#global-nav{
    ul.nav-list{
        display: inline-flex;
        margin: 32px 0;
        list-style: none;
    }
    
}

.item-link{
    text-decoration: none;
    font-size: 25px;
    color: #0E96C9;
    font-family:Noto Sans JP;
    margin-right: 35px;
}